footer {    
    height: 50px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;    
    box-shadow: 0 -3px 10px lightgray;
}

.footer-wrapper {
    text-align: center;
}

.github-link {
    margin-left: 20px;
    color: dodgerblue;
    transition: all 0.3s ease-in;
}

.github-link:hover {    
    color: teal;
}
